import { useQuery } from '@tanstack/react-query';

import { BASE_URL, fetchAuthInstance } from '../instance';

export const getPointPath = () => `${BASE_URL}/api/members/point`;

export const getPoint = async () => {
  const response = await fetchAuthInstance.get(getPointPath());
  return response.data;
};

export const useGetPoint = () =>
  useQuery({
    queryKey: ['point'],
    queryFn: getPoint,
    select: (response) => response.point,
  });
